        filters: {
            formatId(id){ 
                
                let a = id.toString()
                let b = ""
                for(let i=0; i<4-a.length; i++){
                    b+="0"
                }
                let position = 0
                let output = [a.slice(0, position), b, a.slice(position)].join('')
                return output
                
                
            },
            formatDate(value){
                console.log(value)
                if (!value) return ''
                value = value.toString()
                value = value.slice(0, 10).replace(/-/g, ".")
                return value
            }
        },
